<template>
  <b-overlay :show="show" rounded="sm">
    <div>
      <b-row>
        <div class="title-fs-2 ml-5">{{ $t("CUSTOMERINFORMATION") }}</div>
        <br /><br />
      </b-row>
      <div class="ml-5">
        <b-row>
          <b-col xl="5">
            <b-row class="d-flex align-items-center">
              <b-col xl="5">
                <label class="highLightHeaderLabel">{{
                  $t("COMPANYNAMEORNAME")
                }}</label>
              </b-col>
              <b-col>
                <b-form-input
                  :value="clientInfo.client_name"
                  class="form-control"
                  disabled
                >
                </b-form-input>
              </b-col>
            </b-row>
          </b-col>
          <b-col xl="3">
            <b-row class="d-flex align-items-center">
              <b-col xl="auto">
                <label class="highLightHeaderLabel">{{
                  $t("Headquarterslocation")
                }}</label>
              </b-col>
              <b-col>
                <b-form-input
                  :value="clientInfo.value_string_small"
                  class="form-control"
                  disabled
                ></b-form-input>
              </b-col>
            </b-row>
          </b-col>
          <b-col xl="3">
            <b-row class="d-flex align-items-center">
              <b-col xl="auto">
                <label class="highLightHeaderLabel">{{
                  $t("CPERSONINCHARGE")
                }}</label>
              </b-col>
              <b-col>
                <b-form-input
                  :value="clientInfo.client_person_name"
                  class="form-control"
                  disabled
                >
                </b-form-input>
              </b-col>
            </b-row>
          </b-col>
        </b-row>
      </div>
      <br />
      <b-row>
        <div class="title-fs-2 ml-5">
          {{ $t("REDUCTIONPROPOSALINFORMATION") }}
        </div>
        <br /><br />
      </b-row>
      <div class="ml-5">
        <b-row>
          <b-col xl="5" class="mt-2">
            <b-row class="d-flex align-items-center">
              <b-col xl="5">
                <label class="highLightHeaderLabel">{{
                  $t("REDUCTIONPROPOSALMENU")
                }}</label>
              </b-col>
              <b-col>
                <b-form-input
                  :value="reductionInfo.reduction_proposal_name"
                  class="form-control"
                  disabled
                ></b-form-input>
              </b-col>
            </b-row>
          </b-col>
          <b-col class="mt-2">
            <b-button
              @click="showProposalModal"
              :disabled="reductionButton"
              variant="primary"
              class="px-4 align-content-center"
              >{{ $t("SELECTREDUCTIONPROPOSALMENUBTN") }}</b-button
            >
          </b-col>
        </b-row>
        <br />
        <b-row>
          <b-col xl="5">
            <b-row class="d-flex align-items-center">
              <b-col xl="5">
                <label class="highLightHeaderLabel">{{ $t("ACCOUNT") }}</label>
              </b-col>
              <b-col>
                <b-form-input
                  :value="reductionInfo.accounts_name_nm"
                  class="form-control"
                  disabled
                >
                </b-form-input>
              </b-col>
            </b-row>
          </b-col>
          <b-col xl="4">
            <b-row class="d-flex align-items-center">
              <b-col xl="auto">
                <label class="highLightHeaderLabel">{{
                  $t("EXPENSEITEM")
                }}</label>
              </b-col>
              <b-col>
                <b-form-input
                  :value="reductionInfo.expense_item_nm"
                  class="form-control"
                  disabled
                ></b-form-input>
              </b-col>
            </b-row>
          </b-col>
          <b-col xl="3">
            <b-row class="d-flex align-items-center">
              <b-col xl="auto">
                <label class="highLightHeaderLabel">{{
                  $t("PROPOSEDAMOUNT")
                }}</label>
              </b-col>
              <b-col>
                <b-form-input
                  :value="reductionInfo.reduction_proposal_cost"
                  class="form-control"
                  disabled
                >
                </b-form-input>
              </b-col>
            </b-row>
          </b-col>
        </b-row>
        <br />
        <b-row>
          <b-col xl="5">
            <b-row class="d-flex align-items-center">
              <b-col xl="5">
                <label class="highLightHeaderLabel">{{
                  $t("EXPIRATIONDATESTART")
                }}</label>
              </b-col>
              <b-col>
                <b-form-input
                  :value="reductionInfo.effective_date_st"
                  class="form-control"
                  disabled
                >
                </b-form-input>
              </b-col>
            </b-row>
          </b-col>
          <b-col xl="5">
            <b-row class="d-flex align-items-center">
              <b-col xl="5">
                <label class="highLightHeaderLabel">{{
                  $t("EXPIRATIONDATEEND")
                }}</label>
              </b-col>
              <b-col>
                <b-form-input
                  :value="reductionInfo.effective_date_ed"
                  class="form-control"
                  disabled
                ></b-form-input>
              </b-col>
            </b-row>
          </b-col>
        </b-row>
        <br />
      </div>
      <b-form @submit.prevent="submit" class="ml-5">
        <b-row>
          <b-col>
            <b-row class="d-flex align-items-center">
              <b-col xl="1">
                <label class="highLightHeaderLabel">{{
                  $t("PROPOSALTEXT")
                }}</label>
              </b-col>
              <b-col xl="8">
                <div class="ml-5 pl-3">
                  <b-form-textarea
                    v-model="reductionInfo.reduction_proposal_article_template"
                    rows="6"
                    class="f-textarea ml-4"
                  ></b-form-textarea>
                </div>
              </b-col>
            </b-row>
          </b-col>
        </b-row>
      </b-form>
      <br />
      <b-button
        :disabled="!itemSelectedFlag || registerFlag"
        @click="registerProposal"
        variant="primary"
        class="px-4 align-content-center ml-5"
      >
        <span>
          {{ $t("COSTREDUCTIONPROPOSALREGISTRATION") }}
        </span>
        <b-spinner v-if="registerFlag" small></b-spinner
      ></b-button>
      <br /><br /><br />

      <div>
        <b-modal
          id="proposal-modal"
          ref="proposal-modal"
          :hide-footer="true"
          centered
          variant="secondary"
          modal-class="py-0"
          body-class="py-0 mb-3"
          size="lg"
          footer-class="border-0"
          header-class="border-0 bg-white"
          button-size="sm"
        >
          <template v-slot:modal-header="{ cancel }">
            <h5 class="title-fs font-weight-bold mt-2 text-dark ml-3">
              {{ $t("COSTPROPOSALSELECTION") }}
            </h5>
            <b-button
              @click="cancel"
              class="back-btn close-btn mt-2"
              variant="primary"
              >{{ $t("CLOSE") }}</b-button
            >
          </template>
          <hr class="mt-0 mb-2" />
          <div class="m-body">
            <div>
              <b-form inline>
                <b-row>
                  <b-col cols="12" xl="12">
                    <span class="inlineLabel"
                      >&nbsp;{{ $t("REGISTRATIONFEE") }}</span
                    >
                  </b-col>
                </b-row>
                <b-row>
                  <b-col>
                    <b-form-select
                      @change="onExpenseChange($event)"
                      v-model="expense_code"
                      :options="expenseItemOptions"
                      :plain="true"
                      class="w-29"
                      size="sm"
                    ></b-form-select
                  ></b-col>
                </b-row>
              </b-form>
            </div>
            <b-row class="mt-2">
              <b-col xl="12">
                <l-table
                  :hasPagination="false"
                  :showPerPage="false"
                  :isBusy="isBusy"
                  :table-data="tableData"
                  :fields="fields"
                  :outlined="true"
                  :per-page="limit"
                  @onChecked="selectItem"
                  small="small"
                  pagination-align="center"
                  fixed
                  sticky-header
                >
                </l-table>
              </b-col>
            </b-row>
          </div>
          <hr class="mt-0 mb-0" />
          <div class="btn-style1">
            <b-button
              :disabled="checkedItems == ''"
              @click="submitChoice"
              type="submit"
              variant="primary"
              class="float-right send-btn mt-2 m-body"
              >{{ $t("SELECTEXPENSEITEMS") }}
              <i v-if="loading" class="fa fa-spinner fa-spin" />
            </b-button>
          </div>
        </b-modal>
      </div>
    </div>
  </b-overlay>
</template>

<script>
import LTable from "@/components/ListTable";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { getDate } from "@/shared/utils";
import { mapGetters } from "vuex";
import { getDateHourMinute } from "@/shared/utils";
import { endPoint } from "@/shared/endPoint";

export default {
  components: { LTable },
  data() {
    return {
      loading: false,
      expense_code: { expense_item_cd: "", accounts_name_cd: "" },
      isBusy: false,
      registerFlag: false,
      itemSelectedFlag: false,
      tableData: [],
      checkedItems: "",
      status: false,
      expenseItemOptions: [],
      selectedItem: "",
      limit: 0,
      supplierReductionProposalInfoListObject: [],
      clientInfo: {
        client_name: "",
        value_string_small: "",
        client_person_name: "",
        client_mail: "",
      },
      reductionInfo: {
        reduction_proposal_name: "",
        accounts_name_nm: "",
        accounts_name_cd: "",
        expense_item_nm: "",
        expense_item_cd: "",
        reduction_proposal_cost: "",
        effective_date_st: "",
        effective_date_ed: "",
        proposalText: "",
        supplier_no: "",
        supplier_name: "",
        icon_path: "",
        icon_file_name: "",
        reduction_proposal_article_template: "",
      },
      reductionButton: true,
      show: true,
    };
  },
  computed: {
    ...mapGetters({
      client_details: "getClientData",
    }),
    fields() {
      return [
        {
          key: "checked",
          label: "",
          class: "text-center normal-width",
        },
        {
          key: "expense_item_name",
          label: this.$t("EXPENSEITEM"),
          class: "text-center normal-width",
        },
        {
          key: "reduction_proposal_name",
          label: this.$t("REDUCTIONPROPOSALMENUNAME"),
          class: "text-center normal-width",
        },

        {
          key: "created_date",
          label: this.$t("EXPIRATIONDATESTART"),
          class: "text-center normal-width",
        },
        {
          key: "updated_date",
          label: this.$t("EXPIRATIONDATEEND"),
          class: "text-center  normal-width",
        },
        {
          key: "reduction_proposal_cost",
          label: this.$t("PROPOSEDAMOUNT"),
          class: "text-center normal-width",
        },
      ];
    },
  },
  mounted() {
    this.getSupplierData();
    if (this.client_details.client_no != undefined) {
      if (localStorage.getItem("personID")) {
        this.reductionButton = false;
      }
      this.getClientInfo();
    } else {
      this.$router.push({ name: "client_list" });
    }
  },
  methods: {
    /**
     * @description function to validate the login person is available in suplierInfo data
     * @name getSupplierInfoData
     */
    async getSupplierData() {
      const { data } = await BasicInfoApis.postAPI(
        "master/readdata/m_supplier_person_info",
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "person_id",
              value: localStorage.getItem("personID"),
            },
          ],
        }
      );
      if (data.success) {
        let responseData = data.data;
        if (responseData.length < 1) {
          this.reductionButton = true;
        }
      }
    },
    /**
     * @description function to fetch the reductionProposalInfo
     * @name reductionProposalInfo
     */
    async reductionProposalInfo(info) {
      this.show = true;
      const { data } = await BasicInfoApis.postAPI(
        endPoint.CLIENTREADSUPPLIERREDUCTIONPROPOSALINFO,
        {
          supplier_reduction_proposal_info_no: parseInt(info),
          client_no: parseInt(this.client_details.client_no),
        }
      );
      this.show = false;
      if (data.success) {
        this.reductionInfo.reduction_proposal_name =
          data.data.supplierReductionProposalInfoObject.reduction_proposal_name;
        this.reductionInfo.accounts_name_nm =
          data.data.supplierExpenseItemInfoObject.accounts_name_nm;
        this.reductionInfo.expense_item_nm =
          data.data.supplierExpenseItemInfoObject.expense_item_nm;
        this.reductionInfo.reduction_proposal_cost = parseInt(
          data.data.supplierReductionProposalInfoObject.reduction_proposal_cost
        );
        this.reductionInfo.effective_date_st =
          data.data.supplierReductionProposalInfoObject.effective_date_st !=
          null
            ? data.data.supplierReductionProposalInfoObject.effective_date_st.replace(
                /-/g,
                "/"
              )
            : "";
        this.reductionInfo.effective_date_ed =
          data.data.supplierReductionProposalInfoObject.effective_date_ed !=
          null
            ? data.data.supplierReductionProposalInfoObject.effective_date_ed.replace(
                /-/g,
                "/"
              )
            : "";
        this.reductionInfo.supplier_no =
          data.data.supplierExpenseItemInfoObject.supplier_no;
        this.reductionInfo.supplier_name =
          data.data.supplierExpenseItemInfoObject.supplier_name;
        this.reductionInfo.icon_path =
          data.data.supplierExpenseItemInfoObject.icon_path;
        this.reductionInfo.icon_file_name =
          data.data.supplierExpenseItemInfoObject.icon_file_name;
        this.reductionInfo.accounts_name_cd =
          data.data.supplierExpenseItemInfoObject.accounts_name_cd;
        this.reductionInfo.expense_item_cd =
          data.data.supplierExpenseItemInfoObject.expense_item_cd;
        this.reductionInfo.reduction_proposal_article_template =
          data.data.supplierExpenseItemInfoObject
            .reduction_proposal_article_template != null
            ? data.data.supplierExpenseItemInfoObject.reduction_proposal_article_template.replace(
                /<\/?[^>]+(>|$)/g,
                ""
              )
            : "";

        this.itemSelectedFlag = true;
      }
    },
    /**
     * @description function to show the popup-modal
     * @name showProposalModal
     */
    showProposalModal() {
      this.checkedItems = "";
      this.expense_code = { expense_item_cd: "", accounts_name_cd: "" };
      this.getExpenseData();
      this.getExpenseItemList();
      this.$refs["proposal-modal"].show();
    },
    /**
     * @description function to hide the popup-modal
     * @name hideMessageModal
     */
    hideMessageModal() {
      this.$refs["proposal-modal"].hide();
    },
    /**
     * @description function to fetch the checked items in popup-modal
     * @name selectItem
     */
    selectItem(items) {
      this.checkedItems = items;
    },
    /**
     * @description function to fetch the expenseData
     * @name onExpenseChange
     */
    onExpenseChange() {
      this.getExpenseData();
    },
    /**
     * @description function to fetch the expense item list based on the person_id
     * @name getexpenseItemList
     */
    async getExpenseItemList() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.SUPPLIERREADSUPPLIEREXPENSEITEMLIST,
        {
          person_id: localStorage.getItem("personID"),
        }
      );
      if (data.success) {
        let responseData = data.data.expenseItemObjectList;
        let expenseListData = [];
        if (responseData.length) {
          responseData.forEach((item) => {
            let iterateExpenseItem = {
              text: item.expense_item_nm ? item.expense_item_nm : "",
              value: {
                expense_item_cd: item.expense_item_cd
                  ? item.expense_item_cd
                  : "",
                accounts_name_cd: item.accounts_name_cd
                  ? item.accounts_name_cd
                  : "",
              },
            };
            if (item.expense_item_nm) {
              expenseListData.push(iterateExpenseItem);
            }
          });
          this.expenseItemOptions = expenseListData;
        } else {
          this.expenseItemOptions = [];
        }
      } else {
        this.expenseItemOptions = [];
      }
    },
    /**
     * @description function to fetch the expenseData based on the person_id,expense_cd and account_cd
     * @name getExpenseData
     */
    async getExpenseData() {
      this.isBusy = true;
      let compiledArray;
      if (this.expense_code.expense_item_cd == "") {
        compiledArray = {
          expense_item_cd: this.expense_code.expense_item_cd,
          accounts_name_cd: this.expense_code.accounts_name_cd,
          person_id: localStorage.getItem("personID"),
        };
      } else {
        compiledArray = {
          expense_item_cd: this.expense_code.expense_item_cd,
          accounts_name_cd: this.expense_code.accounts_name_cd,
          person_id: localStorage.getItem("personID"),
        };
      }
      const { data } = await BasicInfoApis.postAPI(
        endPoint.CLIENTREADSUPPLIERREDUCTIONPROPOSAL,
        compiledArray
      );
      this.isBusy = false;
      if (data.success) {
        let listObject = data.data.supplierReductionProposalInfoListObject;
        this.supplierReductionProposalInfoListObject = [
          ...data.data.supplierReductionProposalInfoListObject,
        ];
        let mapData = [];
        this.limit = listObject.length;
        listObject.forEach((item, i) => {
          let listData = {
            id: i,
            supplier_expense_item_info_no: item.supplier_expense_item_info_no,
            expense_item_name: item.expense_item_nm,
            reduction_proposal_name: item.reduction_proposal_name,
            reduction_proposal_cost: this.filterByCurrency(
              item.reduction_proposal_cost
            ),

            created_date: getDate(new Date(item.effective_date_st)),
            updated_date: getDate(new Date(item.effective_date_ed)),
          };
          mapData.push(listData);
        });
        this.tableData = mapData;
        this.checkedItems = "";
      }
    },
    /**
     * @description function to convert number to currency format
     * @name filterByCurrency
     */
    filterByCurrency(value) {
      try {
        return parseInt(value)
          .toString()
          .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
      } catch {
        return 0;
      }
    },
    /**
     * @description function to fetch the checked item in popup-modal and close the popup
     * @name submitChoice
     */
    submitChoice() {
      this.$refs["proposal-modal"].hide();
      const checkedItem = this.checkedItems.index;
      this.selectedItem = {
        ...this.supplierReductionProposalInfoListObject[checkedItem],
      };
      this.reductionProposalInfo(
        this.selectedItem.supplier_reduction_proposal_info_no
      );
    },
    /**
     * @description function to register the proposal data
     * @name registerproposal
     */
    async registerProposal() {
      this.registerFlag = true;
      this.reductionButton = true;
      const { data } = await BasicInfoApis.postAPI(
        endPoint.CLIENTCLIENTCOSTREDUCTIONPROPOSALADDEDIT,
        {
          reduction_proposal_type: "002", //
          reduction_proposal_sts_kbn: "01", //
          client_no: this.client_details.client_no,
          client_name: this.client_details.client_name,
          accounts_name_cd: this.reductionInfo.accounts_name_cd,
          expense_item_cd: this.reductionInfo.expense_item_cd,
          supplier_no: this.reductionInfo.supplier_no,
          supplier_name: this.reductionInfo.supplier_name,
          person_id: localStorage.getItem("personID"),
          person_name: localStorage.getItem("clientPersonName"),
          supplier_reduction_proposal_info_no: parseInt(
            this.selectedItem.supplier_reduction_proposal_info_no
          ),
          effective_date_st: this.reductionInfo.effective_date_st,
          effective_date_ed: this.reductionInfo.effective_date_ed,
          reduction_proposal_name: this.reductionInfo.reduction_proposal_name,
          reduction_proposal_cost: this.reductionInfo.reduction_proposal_cost,
          create_id: this.client_details.client_no,
          client_mail: this.clientInfo.client_mail,
          message_dt: getDateHourMinute(new Date()),
          message_type: "002", //
          client_support_type: "006", //
          client_support_action_type: "003", //
          message_sender_account_type: "002", //
          message_sender_account_no: this.reductionInfo.supplier_no,
          message_sender_person_name: localStorage.getItem("clientPersonName"),
          message_sender_belongs: this.reductionInfo.supplier_name,
          message_destination_account_type: "001", //
          message_destination_account_no: parseInt(
            this.client_details.client_no
          ),
          message_destination_person_name: this.clientInfo.client_person_name,
          message_sts_kbn: "00", //
          message_title: this.reductionInfo.reduction_proposal_name,
          message_text: this.reductionInfo.reduction_proposal_article_template, //
          message_sender_icon_path:
            this.reductionInfo.icon_path,
        }
      );
      this.reductionButton = false;
      this.registerFlag = false;
      if (data.success) {
        this.$router.back();
      }
    },
    /**
     * @description function to fetch the client details
     * @name getClientInfo
     */
    async getClientInfo() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADWITHJOINMCLIENTBASEINFO,
        {
          limit: 5,
          offset: 0,
          paginate: true,
          fieldlist: [
            "m_client_base_info.*",
            "m_general_purpose.value_string_small",
          ],
          conditions: [
            {
              key: "m_general_purpose.key",
              value: "PREF",
              operator: "=",
            },
            {
              key: "m_client_base_info.client_no",
              value: parseInt(this.client_details.client_no),
              operator: "=",
            },
          ],
          joins: [
            {
              table: "m_general_purpose",
              join_type: "left",
              join_conditions: [
                {
                  join_using: "m_general_purpose.code",
                  join_operator: "=",
                  join_with: "m_client_base_info.client_main_office_kbn",
                },
              ],
            },
          ],
        }
      );
      this.show = false;
      if (data.success) {
        this.clientInfo.client_name = data.data.data[0].client_name;
        this.clientInfo.value_string_small =
          data.data.data[0].value_string_small;
        this.clientInfo.client_person_name =
          data.data.data[0].client_person_name;
        this.clientInfo.client_mail = data.data.data[0].client_mail;
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.required-btnAny {
  padding: 2px 5px 2px 5px !important;
  min-width: 10px !important;
  font-size: 11px;
  border-radius: 0px;
  cursor: default !important;
}
.inlineLabel {
  padding-right: 10px;
}
.w-29 {
  min-width: 5.6rem !important;
}

.highLightHeaderLabel {
  font-weight: 700 !important;
  font-size: 0.8rem !important;
  padding-top: 9px;
  font-size: initial;
}

.f-textarea {
  max-height: 250px;
  overflow-y: scroll;
}

.reg-fee {
  padding-right: 30px !important;
}
.m-header {
  margin-top: 4px;
  color: black;
}
.modal-body {
  padding: 0 !important;
}
.m-body {
  padding-right: 20px;
  padding-left: 20px;
}
.btn-style1 {
  padding-right: 20px !important;
}
</style>