<template>
  <div>
    <b-row v-if="showPerPage">
      <b-col sm="11" class="mb-3"></b-col>
      <b-col sm="1" class="mb-2">
        <b-form-select
          id="perPageSelect"
          v-model="perPageObjects"
          :options="pageOptions"
          size="sm"
        ></b-form-select>
      </b-col>
    </b-row>
    <b-table
      id="ltable"
      ref="selectableTable"
      :busy="isBusy"
      @row-dblclicked="dbClick"
      :head-variant="headVariant"
      :hover="hover"
      :striped="striped"
      :bordered="bordered"
      :small="small"
      :fields="fields"
      :fixed="fixed"
      :items="items"
      :current-page="serverSideProcess ? 0 : currentPage"
      :per-page="perPage"
      :outlined="outlined"
      :borderless="borderless"
      :sort-by.sync="sortBy"
      :sort-desc.sync="sortDesc"
      :tbody-tr-class="trClass"
      @filtered="onFiltered"
      :filter="filter"
      select-mode="single"
      sticky-header
      responsive="sm"
      class="text-left"
      show-empty
    >
      <template v-slot:cell(checked)="item">
        <b-form-radio
          :id="'radio-' + item.item.id"
          v-model="item.item.checked"
          :name="'table-radio'"
          @change="getSelectedItem(item)"
        ></b-form-radio>
      </template>
      <template v-slot:cell(checked2)="item">
        <b-form-radio
          :id="'radio-' + item.item.id"
          v-model="item.item.checked"
          :name="'table-radio2'"
          @change="getSelectedItem2(item)"
        ></b-form-radio>
      </template>
      <!-- <template v-slot:head(selected)>
        <b-form-checkbox
          :id="'checkbox-All' + name"
          v-model="allSelected"
          @change="selectAll()"
          name="'checkbox-All'"
        ></b-form-checkbox>
      </template> -->
      <template v-slot:cell(selected)="item">
        <b-form-checkbox
          :id="'checkbox-' + item.item.id"
          v-model="item.item.selected"
          :name="'checkbox-' + item.index"
          @change="toggleSelectAll(item)"
        ></b-form-checkbox>
      </template>

      <template v-slot:cell(selected2)="item">
        <b-form-checkbox
          :id="'checkbox-' + item.item.id"
          v-model="item.item.selected"
          :name="'checkbox-' + item.index"
          @change="toggleSelectAll2(item)"
        ></b-form-checkbox>
      </template>

      <template v-slot:cell(test)="item">
        <a href="javascript:void(0)">
          <span @click="test(item)" class="pointer">{{ $t("TEST") }}</span>
        </a>
      </template>
      <template v-slot:cell(access)="item">
        <span @click="roleAccess(item)" class="pointer">
          <i class="fa fa-key" aria-hidden="true"></i>
        </span>
      </template>

      <template v-slot:cell(delete)="item">
        <i
          @click="remove(item)"
          :title="$t('DELETE')"
          class="fa fa-trash cursor-pointer"
          aria-hidden="true"
        ></i>
      </template>
      <template v-slot:cell(edit)="item">
        <b-button @click="edit(item)" variant="primary" class="btn-width">{{
          $t("EDIT")
        }}</b-button>
      </template>
      <template v-slot:cell(same_scale_evaluation_sales)="item">
        <span v-if="item.value != '-'">
          <img
            :src="getImage(item)"
            class="text-muted"
            aria-hidden="true"
            width="60%"
            alt="icon"
          />
        </span>
        <span v-else>-</span>
      </template>
      <template v-slot:head(same_scale_evaluation_sales)="item">
        <span>{{ item.field.label }}</span>
        <div>{{ item.field.sublabel }}</div>
      </template>
      <template v-slot:head(same_scale_evaluation_employee)="item">
        <span>{{ item.field.label }}</span>
        <div>{{ item.field.sublabel }}</div>
      </template>
      <template v-slot:head(same_scale_evaluation_tsubo)="item">
        <span>{{ item.field.label }}</span>
        <div>{{ item.field.sublabel }}</div>
      </template>
      <template v-slot:head(overall_evaluation_year)="item">
        <span>{{ item.field.label }}</span>
        <div>{{ item.field.sublabel }}</div>
      </template>
      <template v-slot:head(amount_reduction_year)="item">
        <span>{{ item.field.label }}</span>
        <div class="text-center">{{ item.field.sublabel }}</div>
      </template>
      <template v-slot:cell(amount_reduction_year)="item">
        <span class="amountDisplay">{{ item.value }}</span>
      </template>

      <template v-slot:cell(same_scale_evaluation_employee)="item">
        <span v-if="item.value != '-'">
          <img
            :src="getImage(item)"
            class="text-muted"
            aria-hidden="true"
            width="60%"
            alt="icon"
          /> </span
        ><span v-else>-</span>
      </template>
      <template v-slot:cell(same_scale_evaluation_tsubo)="item">
        <span v-if="item.value != '-'">
          <img
            :src="getImage(item)"
            class="text-muted"
            aria-hidden="true"
            width="60%"
            alt="icon"
          />
        </span>
        <span v-else>-</span>
      </template>
      <template v-slot:cell(expense_item)="item">
        <b-link @click="seeExpenseDetails(item)">
          <u>{{ item.value }}</u>
        </b-link>
      </template>
      <template v-slot:cell(reduction_plan)="item" )>
        <b-link v-if="item.value" @click="seeMore(item)">
          <img
            src="@/assets/plan-icon.png"
            class="text-muted icon-background"
            aria-hidden="true"
            width="60%"
            alt="icon"
          />
        </b-link>
      </template>
      <template v-slot:cell(color)="item">
        <span :style="{ background: item.item.color }" class="dot"></span>
      </template>
      <template v-slot:cell(client_name)="item">
        <b-link @click="clientClick(item)">
          <u>{{ item.value }}</u>
        </b-link>
      </template>
      <template v-slot:cell(standard_monthly_fee)="item">
        <div>{{ item.value }}{{ item.value ? $t("YEN") : "" }}</div>
      </template>
      <template v-slot:cell(reduction_upper_limit)="item">
        <div>{{ item.value }}{{ item.value ? "%" : "" }}</div>
      </template>
      <template v-slot:cell(reduction_lower_limit)="item">
        <div>{{ item.value }}{{ item.value ? "%" : "" }}</div>
      </template>
      <template v-slot:cell(sense_of_cost_up)="item">
        <div>{{ item.value }}{{ item.value ? $t("YEN") : "" }}</div>
      </template>
      <template v-slot:cell(sense_of_cost_low)="item">
        <div>{{ item.value }}{{ item.value ? $t("YEN") : "" }}</div>
      </template>
      <template v-slot:cell(industry_average)="item">
        <div>{{ item.value }}{{ item.value ? $t("YEN") : "" }}</div>
      </template>

      <template v-slot:empty>
        <div class="text-center errorColor">
          {{ $t("GENRALDATAERRORMESSAGE") }}
        </div>
      </template>
      <!-- <template v-slot:emptyfiltered>
        <div class="text-center">{{ $t("FILTEREDRECORD") }}</div>
      </template> -->
      <template v-slot:table-busy>
        <div class="text-center text-danger my-2">
          <b-spinner class="align-middle"></b-spinner>
        </div>
      </template>
    </b-table>
    <b-pagination
      v-if="hasPagination"
      v-model="currentPage"
      :total-rows="totalRows"
      :per-page="perPage"
      :align="paginationAlign"
      size="md"
    >
      <template v-slot:prev-text>{{ $t("PREVIOUS") }}</template>
      <template v-slot:next-text>{{ $t("NEXT") }}</template>
      <template v-slot:page="{ page, active }">
        <strong v-if="active">{{ page }}</strong>
        <i v-else>{{ page }}</i>
      </template>
    </b-pagination>
  </div>
</template>

<script>
import _ from "lodash";
import { mapGetters } from "vuex";
import { findImage } from "@/shared/utils";

export default {
  name: "LTable",
  components: {},
  inheritAttrs: false,
  props: {
    caption: {
      type: String,
      default: "Table",
    },
    hover: {
      type: Boolean,
      default: false,
    },
    headVariant: {
      type: String,
      default: "light",
    },
    trClass: {
      type: String,
      default: "",
    },
    outlined: {
      type: Boolean,
      default: true,
    },
    striped: {
      type: Boolean,
      default: false,
    },
    showPerPage: {
      type: Boolean,
      default: false,
    },
    bordered: {
      type: Boolean,
      default: false,
    },
    borderless: {
      type: Boolean,
      default: true,
    },
    small: {
      type: Boolean,
      default: false,
    },
    fixed: {
      type: Boolean,
      default: true,
    },
    isBusy: {
      type: Boolean,
      default: false,
    },
    hasPagination: {
      type: Boolean,
      default: false,
    },
    tableData: {
      type: [Array, Function],
      default: () => [],
    },
    fields: {
      type: [Array, Object],
      default: () => [],
    },
    perPage: {
      type: Number,
      default: 0,
    },
    filter: {
      type: String,
      default: "",
    },
    dark: {
      type: Boolean,
      default: true,
    },
    dbClick: {
      type: Function,
      default: () => [],
    },
    selectItemsToDelete: {
      type: Function,
      default: () => [],
    },
    total: {
      type: Number,
      default: 0,
    },
    startPage: {
      type: Number,
      default: 0,
    },
    serverSideProcess: {
      type: Boolean,
      default: false,
    },
    modalShowState: {
      type: Boolean,
      default: false,
    },
    showSettings: {
      type: Boolean,
      default: false,
    },
    showEdit: {
      type: Boolean,
      default: true,
    },
    showDelete: {
      type: Boolean,
      default: false,
    },
    showActivate: {
      type: Boolean,
      default: false,
    },

    sortField: {
      type: String,
      default: "",
    },
    sortByDesc: {
      type: Boolean,
      default: false,
    },
    childAllSelected: {
      type: Boolean,
      default: false,
    },
    paginationAlign: {
      type: String,
      default: "right",
    },
    name: {
      type: String,
      default: "",
    },
  },
  data: () => {
    return {
      currentPage: 1,
      allSelected: false,
      allSelected2: false,

      checkedItems: [],
      checkedItems2: [],

      selectedRow: false,
      pageOptions: [10, 25, 50, 100],
      perPageObjects: 10,
      sortBy: "",
      sortDesc: false,
    };
  },
  validations: {},
  computed: {
    ...mapGetters({
      getModalShow: "getModalShow",
    }),
    items() {
      return this.tableData;
    },
    totalRows() {
      if (this.total) {
        return this.total;
      }
      return this.getRowCount();
    },
  },
  watch: {
    perPageObjects() {
      this.$emit("change-per-page", this.perPageObjects);
    },
    sortBy() {
      this.$emit("sort", this.sortBy, this.sortDesc);
    },
    sortDesc() {
      this.$emit("sort", this.sortBy, this.sortDesc);
    },
    checkedItems() {},
    currentPage() {
      this.allSelected = false;
      this.checkedItems = [];
      this.$emit("change-page", this.currentPage);
    },
    startPage() {
      this.currentPage = this.startPage + 1;
    },
    items() {
      if (Object.keys(this.items).length != 0) {
        this.items.map(function(currentValue) {
          currentValue.selected = false;
        });
      }
    },
  },
  created() {
    this.sortBy = this.sortField;
    this.sortDesc = this.sortByDesc;
  },
  methods: {
    getImage(item) {
      return findImage(item.value);
    },
    getSelectedItem(item) {
      this.$emit("onChecked", item);
    },
    getSelectedItem2(item) {
      this.$emit("onChecked2", item);
    },
    getRowCount: function() {
      return this.items.length;
    },
    selectAll() {
      let count = 0;
      if (!this.allSelected) {
        this.items.map(function(currentValue) {
          count++;
          if (currentValue.role_id == 1) {
            currentValue.selected = false;
          } else {
            currentValue.selected = true;
          }
        });
        this.selectItemsToDelete(this.items);
        this.checkedItems = this.items.filter((item) => {
          if (item.selected == true) {
            return item;
          }
        });
      } else {
        this.items.map(function(currentValue) {
          count++;
          currentValue.selected = false;
        });
        this.selectItemsToDelete([]);
        this.checkedItems = [];
      }
      if (count === this.items.length) {
        this.$emit("onSelect", _.map(this.checkedItems, "id"));
      }
    },
    selectAll2() {
      let count = 0;
      if (!this.allSelected2) {
        this.items.map(function(currentValue) {
          count++;
          if (currentValue.role_id == 1) {
            currentValue.selected = false;
          } else {
            currentValue.selected = true;
          }
        });
        this.selectItemsToDelete(this.items);
        this.checkedItems2 = this.items.filter((item) => {
          if (item.selected == true) {
            return item;
          }
        });
      } else {
        this.items.map(function(currentValue) {
          count++;
          currentValue.selected = false;
        });
        this.selectItemsToDelete([]);
        this.checkedItems = [];
      }
      if (count === this.items.length) {
        this.$emit("onSelect2", _.map(this.checkedItems2, "id"));
      }
    },
    onFiltered(filteredItems) {
      this.$emit("filtered", filteredItems);
    },
    test(item) {
      this.$emit("onTest", item);
    },
    roleAccess(item) {
      this.$emit("onAccess", item);
    },
    activate(item) {
      this.$emit("onActivate", item);
    },

    edit(item) {
      this.$emit("onEdit", item);
    },
    remove(item) {
      this.$emit("onDelete", item);
    },
    seeExpenseDetails(item) {
      this.$emit("onExpenseDetails", item);
    },
    seeMore(item) {
      this.$emit("onSeeMore", item);
    },
    settings(item) {
      this.$emit("onSettings", item);
    },
    clientClick(item) {
      this.$emit("onClientClick", item);
    },
    toggleSelectAll(item) {
      item.item.selected = !item.item.selected;
      if (item.item.role_id == 1) {
        item.item.selected = false;
      }
      this.checkedItems = this.items.filter((checkedItem) => {
        if (checkedItem.selected == true) {
          return checkedItem;
        }
      });
      if (this.checkedItems.length === this.items.length) {
        this.allSelected = true;
      } else {
        this.allSelected = false;
      }
      this.$emit("onSelect", _.map(this.checkedItems, "id"));
      setTimeout(() => {
        this.selectItemsToDelete(
          this.items.filter((selectedItem) => selectedItem.selected == true)
        );
      }, 0);
    },
    toggleSelectAll2(item) {
      item.item.selected = !item.item.selected;
      if (item.item.role_id == 1) {
        item.item.selected = false;
      }
      this.checkedItems2 = this.items.filter((checkedItem) => {
        if (checkedItem.selected == true) {
          return checkedItem;
        }
      });
      if (this.checkedItems2.length === this.items.length) {
        this.allSelected = true;
      } else {
        this.allSelected = false;
      }
      this.$emit("onSelect2", _.map(this.checkedItems2, "id"));
      setTimeout(() => {
        this.selectItemsToDelete(
          this.items.filter((selectedItem) => selectedItem.selected == true)
        );
      }, 0);
    },
  },
};
</script>
<style lang="scss">
$primary: #062854 !default;

.btn-color {
  background-color: $primary;
  color: white;
  border-color: none;
  border-color: rgb(172, 0, 12);
}
.amountDisplay {
  color: #f90af5;
}
.selected-row {
  color: #23282c;
  background-color: rgba(0, 0, 0, 0.075);
  border: 1px solid $primary;
}
.cursor-pointer {
  cursor: pointer;
}
.dot {
  height: 15px;
  width: 15px;
  background-color: #bbb;
  border-radius: 50%;
  display: inline-block;
}
.inherit-display {
  display: block ruby;
  display: inline-flex;
}
.errorColor {
  color: red;
}
.btn-width {
  min-width: 4rem;
  width: 4rem;
}
</style>
